html{
  font-size:62.5%;
  box-sizing:border-box;
  margin: 0;
  padding: 0;
  background:lightgray;
}

.yo{
  color:navy;
  font-size:8rem;
  text-align:center;
  
}
.intro-page{
  
  align:center;
 
  
}
.green{
  background:green;
  color:white;
  font-size:2.5rem;
  border-radius: 3rem;
  justify-items: center;
}
.red{
  background:red;
  color:white;
  font-size:2.5rem;
  border-radius: 3rem;
  justify-items: center;
}
.start{
  margin-left: 44%;
  font-size:4rem;
  margin-top: 3%;
}
.hidden{
  display:none;
  
}
#quiz-content{
  
  align-items:center;
  text-align:center;
  font-size: 3.5rem;
  
}
.choices{
  font-size:2.5rem;
  border-radius: 3rem;
  justify-items: center;
  
}
.submit{
  font-size:2.5rem;
  border-radius: 3rem;
  background:lightgray;
}
.choice-container{
  display:flex;
  
  justify-content:space-between;
  align-items: center;
  margin: 0;
  padding: 5% 15% 5% 15%;
  
}
#resultspage{
  text-align:center;
  color:navy;
  font-size:4rem;
  justify-content:space-between;
  
}
#restart{
  font-size:3rem;
  margin-top:5%;
}